/* src/components/ResponsiveContainer.css */
.custom-container {
    /* Set default width to 100% for small screens */
    width: 100%;
    max-width: 100%;
    transition: width 0.3s ease-in-out;
  }
  
  /* For medium screens (992px and up) */
  @media (min-width: 992px) {
    .custom-container {
      width: 70%;
    }
  }
  
  /* For large screens (1200px and up) */
  @media (min-width: 1200px) {
    .custom-container {
      width: 70%;
    }
  }
  